<template>
    <div class="appbox">
        <div class="contain" v-if="setNew">
            <div class="title">上传微信二维码</div>
            <div class="subContain pr" >
                <img src="@/static/images/my/shangchuan_image.png" alt  />
                <input @change="headImgChange" type="file" accept="image/*" id="upBtn" />
            </div>
        </div>
        <div class="wechat" v-else>
            <img width="80%" :src="wechat" alt  />
            <button @click="clearCode">重新上传</button>
        </div>
    </div>

</template>

<script>
    import { uploadWechat } from "@/api/user"
    import Toast from 'vant/lib/toast'
    import 'vant/lib/toast/style'
    import { mapGetters } from 'vuex'

    export default {
        name: "Wechatcode",
        computed: {
            ...mapGetters([
                'wechat',
            ])
        },
        data()
        {
            return {
                setNew:false
            }
        },
        mounted()
        {
            this.setNew = this.wechat ? false : true
        },
        methods:{

            headImgChange( e ) {

                let file = e.target.files[0]
                /* eslint-disable no-undef */
                let param = new FormData() // 创建form对象
                param.append('file', file, file.name) // 通过a
                uploadWechat(param).then(res => {

                    this.setNew = false
                    this.$store.dispatch('user/setWechat', res.imgUrl).then(() => {

                    }).catch(() => {

                    })
                })
            },
            clearCode()
            {
                this.setNew = true
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/wechatcode.css";
    .wechat
    {
        width: 100%;
        text-align: center;
        margin: 20px 0;
    }
    .wechat button {
        margin-top: 20px;
        width: 3.45rem;
        height: 0.45rem;
        background: #E33C3D;
        font-size: 0.17rem;
        color: #F5F5F5;
    }
    .wechat img{
        width: 1.4rem;
        height: 1.4rem;
    }
</style>



